.list-item {
    min-height: 160px;
    position: relative;
    margin-left: 120px;
}

.list-item:after {
    content: "";
    position: absolute;
    top: 158px;
    width: 100%;
    height: 2px;
    background: red;
    left: 0px;
}

.list-item:before {
    content: "";
    border: 23px solid transparent;
    border-top-color: black;
    position: absolute;
    top: 0px;
    left: 0px;

}

.list-item-0:before {
    border-top-color: #3a3c43;
}

.list-item-1:before {
    border-top-color: #9da939;
}

.list-item-2:before {
    border-top-color: #aec1bf;
}

.list-item-3:before {
    border-top-color: #eebb3a;
}

.list-item-4:before {
    border-top-color: #b83843;
}

.list-item section {
    position: relative;
}

.list-item section .circle {
    position: relative;
    width: 50px;
    height: 50px;
    top: 0px;
    border-radius: 50%;
    left: 0px;
    background: blue;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-top: 47px; */
}

.list-item section .circle img {
    max-width: 70%;
    max-height: 64%;
}

.section-title-container {
    display: flex;
    height: 160px;
    width: 100%;
    align-items: center;
    column-gap: 20px
}

.section-title-text {
    color: #fff;
    font-size: 30px;
    font-weight: 700;
}